import radioItem from '../../../components/radioItem.vue';
export default {
	data() {
		return {
			goodImg: '/static/image/pic.jpg',
			goodsCode: '0102003',
			goodsName: '测试货品',
			goodsType: '毛衣',
			goodsBrand: '七匹狼',
			goodsYear: '12',
			goodsSeason: '春季',
			supplier: '测试厂商',
			supplierCode: '101010101',
			purchasePrice: '99.5',
			retailSales: '108',
			tradePrice: '102',
			goodsColor: '红色',
			//
			goodsInfo: {},
			isShowRadio: false,
			selRadioType: 1,
			selRadioList: [],
			// 分类
			selClassInfo: {},
			classS: [
				{id: 1,name: '上衣'},
				{id: 2,name: '裙子'},
				{id: 3,name: '裤子'}
			],
			brandS: [
				{id: 1,name: '花花公子'},
				{id: 2,name: '耐克'},
				{id: 3,name: '香奈儿'},
				{id: 4,name: '迪奥'}
			]
		}
	},
	methods: {
		goSelRadio(type) { // 打开单选列表
			this.selRadioType = type;
			this.isShowRadio = true;
			if (type === 1) {
				this.selRadioList = this.classS;
			} else if (type === 2) {
				this.selRadioList = this.brandS;
			}
		},
		radioChange (val) { // 选择分类/品牌
		  this.selClassInfo = val;
		},
		okRadioValue () { // 确定选择类别/品牌
			this.isShowRadio = false;
			if (this.selRadioType === 1) {
				this.goodsInfo.className = this.selClassInfo.name;
			} else if (this.selRadioType === 2) {
				this.goodsInfo.brandName = this.selClassInfo.name;
			}
		}
	},
	components: {
		radioItem
	}
}
